.prices{
    margin-right: 32px;
    @media screen and (max-width: 768px){
        margin-right: 16px;
    }
    &__table{
        display: flex;
        flex-direction: column;
        width: 100%;
        margin: 0 auto;
        flex-wrap: wrap;
        white-space: nowrap;
        font-family: "TTLakes-Regular";
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.2px;
        background-color:$background;

        @media screen and (max-width: 768px){
            margin-right: 16px;
            overflow-x: scroll;
            flex-direction: row;
            flex-wrap: nowrap;
            &::-webkit-scrollbar {
                display: none;
            }
        } 
    }
    &__row{
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        height: 64px;
        white-space: nowrap;
        background: #fff;
        margin-right: 32px;
        border-top: 1px solid rgba(234, 234, 234, 1);

        @media screen and (max-width: 768px){
            height: 200px;
            width: 260px;
            margin-right: 16px;
            padding: 16px;
        } 

        &:first-child{
            background: $background;
            border: none;
            @media screen and (max-width: 768px){
                display: none;
            }
        }
        &:nth-child(2){
            border: none;
        }

        @media screen and (max-width: 768px){
            width: 260px;
            height: 200px;
            display: block;
        }
    }
    &__cell1{
        width: 348px;
        padding-left: 24px;
        position: relative;
        @media screen and (max-width: 768px){
            display: block;
            white-space:initial;
            padding-left: 0;
            padding-top: 20px; 
            &::after{
                content: 'Ремонтные услуги';
                position: absolute;
                color: rgba(126, 126, 130, 1);
                top: 0px;
                left: 0;
            }
        }
    }

    &__cell2{
        width: 74px;
        position: relative;
        @media screen and (max-width: 768px){
            display: block;
            white-space: normal;
            padding-left: 0;
            padding-top: 36px; 

            &::after{
                content: 'Цена';
                position: absolute;
                color: rgba(126, 126, 130, 1);
                top: 16px;
                left: 0;
            }
        }
    }
    &__cell3{
        width: 81px;
        position: relative;
        @media screen and (max-width: 768px){
            display: block;
            white-space: normal;
            padding-left: 0;
            padding-top: 36px; 

            &::after{
                content: 'Срок';
                position: absolute;
                color: rgba(126, 126, 130, 1);
                top: 16px;
                left: 0;
            }
        }
    }
    &cell__4{
        width: 119px;
    }
    &__link{
        width: 119px;
        height: 32px;
        color: white;
        background-color: rgba(255, 62, 121, 1);
        border-radius: 20px;
        cursor: pointer;
        font-family: "TTLakes-Regular";
        font-size: 12px;
        line-height: 24px;
        letter-spacing: -0.15px;
        text-transform: uppercase;
        padding-left: 12px;
        padding-top: 4px;
        margin-right: 32px;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            width: 7px;
            height:2px;
            background-color: $white;
            border-radius: 0 25% 25% 0;
            left: 100px;
            top: 16px;
            rotate: -45deg;
        }
        &::before{
            content: '';
            position: absolute;
            width: 7px;
            height: 2px;
            background: $white;
            left: 100px;
            top: 12px;
            rotate: 45deg;
        }
        @media screen and (max-width: 768px){
            display: inline-block;
            margin-left: 100px;
            margin-bottom: 20px;
        }
    }
    &__title{
        color: $menu-text-color;
    }
    &__hidden{
       opacity: 0;
       cursor: default;
    }
    &__info{
        margin-top: 32px;
        width: 420px;
        font-family: "TTLakes-Regular";
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2px;
        position: relative;
        margin-bottom: 16px;

        @media screen and (max-width: 768px){
            width: 100%;
        }
    }
    &__pdf{
        font-family: "TTLakes-Regular";
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            width: 5px;
            height:2px;
            background-color: rgba(255, 62, 121, 1);
            border-radius: 0 25% 25% 0;
            left: 305px;
            top: 11px;
            rotate: -45deg;
            @media screen and (max-width: 768px){
                left: 110px;
                top: 37px;
            }
        }
        &::before{
            content: '';
            position: absolute;
            width: 5px;
            height:2px;
            background-color: rgba(255, 62, 121, 1);
            border-radius: 0 25% 25% 0;
            left: 305px;
            top: 8px;
            rotate: 45deg;
            @media screen and (max-width: 768px){
                left: 110px;
                top: 34px;
            }
        }

        @media screen and (max-width: 768px){
            display: block;
            width: 200px;
            white-space: normal;
        }
    }
}

.hr{
    width: 95%;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 24px;
    margin-right: 24px;
    height: 1px;
    color:rgba(234, 234, 234, 1);

    @media screen and (max-width: 768px){
        display: none;
    }
    }
