.footer{
    margin-top: 50px;
    height: 65px;
    border-top: 1px solid rgba(217, 255, 245, 1);
    display: flex;
    background-color: rgba(248, 248, 248, 1);
    margin-bottom: 21px;
    justify-content: space-between;
    color: rgba(126, 126, 130, 1);
    font-family:"TTLakes-Regular";
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: right;
    padding: 32px;

    @media screen and (max-width: 768px){
        flex-direction: column;
        padding: 16px;
        text-align: center;
        column-gap: 12px;
    }

    &__rights{
        text-align: left;
        @media screen and (max-width: 768px){
            text-align: center;
        }
    }
}