.header__tablet{
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    height: 88px;
    width: 100%;
    padding: 24px;
    margin-bottom: 92px;
    border-bottom: 1px solid rgba(217, 255, 245, 1);
    position: relative;

    &-burger{
        margin-right: 16px;
        position: relative;
        position: relative;
        display: inline-block;
}
    &-logo-burger{
        display: flex;
        min-width: 180px;
        justify-content: space-between;
        


        @media screen and (max-width: 768px){
            min-width: 0;
        }

    }

    &-other{
        display: flex;
        min-width: 300px;
        justify-content: space-between;

        @media screen and (max-width: 768px){
            justify-content: flex-end;
            min-width: 10px;
        }
        @media screen and (max-width: 768px){
            &-call,
            &-chat,
            &-profile{
                display: none;

            }
        }
        &-repair{

            @media screen and (max-width: 768px){
                margin-right: 16px;
            }

        }
        &-call{
            cursor: pointer;

        }
    }
    @media screen and (min-width: 1120px) {
        display: none;
    }

    @media screen and (max-width: 768px) {
        margin-bottom: 92px;
    }
}


.img__burger{
    position: relative;
    &::after{
        content: "";
        position: absolute;
        width: 2px;
        height: 32px;
        background-color: rgba(234, 234, 234, 1);
        left: 60px;
        top: 6px
    }
}

.img__profile{
    position: relative;
    margin-right: 20px;
    &::after{
        content: "";
        position: absolute;
        width: 2px;
        height: 32px;
        background-color: rgba(234, 234, 234, 1);
        left: 60px;
        top: 6px
    }
}