.modal{
    width: 320px;
    height: 100%;
    padding: 16px;
    background: $background;
    position: fixed;
    z-index: 1;
    top: 0;
    right: -560px;
    border-right: 1px solid rgba(217, 255, 245, 1);
    -webkit-box-shadow: -4px 0px 21px 0px rgba(87,87,87,1);
    -moz-box-shadow: -4px 0px 21px 0px rgba(87,87,87,1);
    box-shadow: -4px 0px 21px 0px rgba(87,87,87,1);
    transition: 0.8s ease-in-out;
    &__title{
        color: $main-text-color;
        font-family: "TTLakes-Regular";
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.6px;
        position: relative;
        &::before{
            content: "";
            display: inline-block;
            position: absolute;
            width: 4px;
            height: 32px;
            background-color: $main-color;
            left: -16px;
            top: 16px;
            border-radius: 0px 10px 10px 0px;
            transform: translateY(-50%);
        }
    }
    &__tel{
        margin-top: 24px;
        border: 1px solid rgba(234, 234, 234, 1);
        border-radius: 5px;
        width: 288px;
        height: 48px;
        padding-left: 16px
    }
    &__agreement{
        margin-top: 16px;
        font-family: "TTLakes-Regular";
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0px;
        color: rgba(126, 126, 130, 1);
    & span{
        color: rgba(255, 62, 121, 1);
    }
    }
    &__link{
        margin-top: 16px;
        float: right;
        padding-right: 32px;
        width: 136px;
        height: 32px;
        color: white;
        background-color: rgba(255, 62, 121, 1);
        border-radius: 20px;
        cursor: pointer;
        font-family: "TTLakes-Regular";
        font-size: 12px;
        line-height: 24px;
        letter-spacing: -0.15px;
        text-transform: uppercase;
        padding-left: 22px;
        padding-top: 4px;
        margin-right: 32px;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            width: 7px;
            height:2px;
            background-color: $white;
            border-radius: 0 25% 25% 0;
            left: 112px;
            top: 16px;
            rotate: -45deg;
        }
        &::before{
            content: '';
            position: absolute;
            width: 7px;
            height: 2px;
            background: $white;
            left: 112px;
            top: 12px;
            rotate: 45deg;
        }

    
    }
    &__close-call{
        position: static;
        left: -64px;
        &:hover{
            left: -60px;
        }
    }
}