.services{
    margin-bottom: 50px;
    &__list{
        margin-top: 24px;
        display: flex;
        flex-wrap: wrap;
        column-gap: 32px;
        row-gap: 16px;
        height: 165px;
        overflow: hidden;


        @media screen and (max-width: 768px) {
            overflow-x: scroll;
            flex-direction: row;
            flex-wrap: nowrap;
            scrollbar-width: none;
            &::-webkit-scrollbar {
                display: none;
              }
        }
    }
    &__item{
        width: 240px;
        height: 160px;
        border: 1px solid #EAEAEA;
        background: $white;
        border-radius: 5px;
    }

    &__link{
    display: inline-block;
    width: 240px;
    height: 160px;
    font-family: "TTLakes-Regular";
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: $main-text-color;
    padding-left: 16px;
    padding-top: 16px;

    }
    &__svg{

        float: right;
        margin-right: 16px;
        margin-top: calc(102px - 16px);
    }
    &__read-more, &__hide-more{
        display: block;
        margin-top: 24px;
        font-family: "TTLakes-Regular";
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;

        @media screen and (max-width: 768px) {
            display: none;
        }

    }
    .details__arrows{
        margin-right: 16px;
    }
    .hidden{
        display: none;
    }
}


