.header{
  padding-top: 42px;
  display: flex;
  justify-content: space-between;
  height: 90px;

  @media screen and (max-width: 1120px) {
    display: none;
  }



  &__menu{
    display: flex;
    align-items: baseline;
    row-gap: 32px;
    margin-right: 32px;
  }
  &__title{
    font-family: "TTLakes-Regular";
    font-size: 28px;
    line-height: 40px;
    letter-spacing: -0.6px;
    position: relative;
    @media screen and (max-width: 1120px) {
       position: absolute;
       margin-top: 113px;
    }
    @media screen and (max-width:768px) {
      margin-top: 112px;
    }
  &::after{
    content: "";
    display: inline-block;
    position: absolute;
    width: 4px;
    height: 32px;
    background-color: $main-color;
    left: -34px;
    top: 20px;
    border-radius: 0px 10px 10px 0px;
    transform: translateY(-50%);

    @media screen and (max-width: 1120px) {
      left: -24px;
   }
    } 
  }

  &__link{
    display: flex;
    margin-right: 32px;
    align-items: center;
  }
  &__img{
    margin-left: 8px;
  }
  &__span{
    @media screen and (max-width: 1120px) {
      display: none;
    }
  }
}

