.modal{
    &__text{
        width: 440px;
        height: 100%;
        padding: 32px;
        background: $background;
        position: fixed;
        z-index: 1;
        top: 0;
        right: -560px;
        overflow: auto;
    }

    &__name, &__email, &__message{
        margin-top: 24px;
        border: 1px solid rgba(234, 234, 234, 1);
        border-radius: 5px;
        width: 288px;
        height: 48px;
        padding-left: 16px
    }
    &__message{
        padding-top: 12px;
        height: 118px;
    }
    &__close-text{
        position: static;
        left: -64px;
        &:hover{
            left: -60px;
        }
    }
}