.info__list{
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }

    @media screen and (max-width: 1120px) {
        flex-wrap: nowrap;
        overflow-x: auto;
        overflow-y: hidden;
        white-space: nowrap;
        -ms-overflow-style: none;
        margin-bottom: 32px;
     }
}
.info__item{
    font-family: "TTLakes-Regular";
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: rgba(126, 126, 130, 1);
    border: 2px solid transparent;
    height: 40px;
    padding: 8px 15px;
    white-space: nowrap;
    // margin-left: 10px;
&:hover{
    background: $white;
    border: 2px solid $border-color;
}
}

.text{
    display: flex;
    margin-top: 10px;

    @media screen and (max-width: 768px) {
        display: block;
    }
    &__main{
        margin-right: 36px;
        font-family: "TTLakes-Regular";
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2px;
        color: $main-text-color;
        position: relative;
        max-height: 200px;

        @media screen and (max-width: 768px) {
            max-height: 406px;
        }
    }
    &__read-more, &__hide-more{
        display: block;
        margin-top: 24px;
        font-family: "TTLakes-Regular";
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;

    }
    .details__arrows{
        margin-right: 16px;
    }
    .hidden{
        display: none;
    }

    &__img{
        position: static;
        max-width: 50%;
        height: auto;
        margin-right: 32px;

        @media screen and (max-width: 1120px) {
            max-width: 40%;
            height: auto;
        }
        @media screen and (max-width: 768px) {
            position: relative;
            display: block;
            max-width: 100%;
            margin-right: 16px;

            & img {
                border-radius: 5px;
            }
        }
    }
}

