.aside{
    float: left;
    padding-top: 35px;
    width: 320px;
    height: 1400px;
    display: flex;
    flex-direction: column;
    background: $white;
    transition: 0.8s all ease;
    position: fixed;
    height: 100vh; /* 100% высоты экрана */
    overflow: scroll; /* Запрещаем скролл внутри aside */

    @media screen and (max-width: 1120px) {
        position: fixed;
        top: 0;
        left: -500px;
        max-width: 860px;
    }

    &__list{
        padding-top: 43px;
        display: flex;
        min-height: 368px;
    }
    &__nav{
        display: flex;
        justify-content: space-between;
        padding-left: 15px;
        padding-right: 15px;
        position: relative;
    }
}

.showAside{
    position: absolute;
    left: 0;
    z-index: 2;
    box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
    transition: 0.5s ease all;
}

.list{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 30px;
    &__item{
        padding-left: 24px;
        position: relative;
        &:hover{
            color: $menu-text-color;
        }
        &:hover::after{
                content: "";
                display: inline-block;
                position: absolute;
                width: 4px;
                height: 32px;
                background-color: $main-color;
                left: 0px;
                top: 8px;
                border-radius: 0px 10px 10px 0px;
                transform: translateY(-50%);
        }

    }
}

.burger{
    &__close{
        display: none;
    }
    &__footer{
        margin-top: 40px;

        @media screen and (max-width: 768px) {
            margin-top: 240px;
        }
        &-contacts{
            padding-top: 17px;
            display: flex;
            flex-direction: column;
            gap: 13px;
            padding-left: 24px;
        }
    }
    &__items{
        display: flex;
        gap: 16px;
        padding-left: 24px;
    }
}
.langs{
    text-transform: uppercase;
    padding: 41px 0px 20px 24px;
}

.lang{
    padding-right: 7px;
    &:hover{
        color: $menu-text-color;
    }
}

.tel{
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;

}
