.brands{
    margin-bottom: 50px;
    margin-top: 50px;
    &__list{
        margin-top: 24px;
        display: flex;
        flex-wrap: wrap;
        row-gap: 16px;
        column-gap: 32px;
        height: 160px;
        overflow-x: hidden;
        overflow-y: hidden;
        
        @media screen and(max-width: 768px) {
            overflow: visible;
            flex-wrap: nowrap;
            height: 120px;
            margin-bottom: 20px;
        }

    }
    &__item{
        width: 240px;
        height: 72px;
        border: 1px solid rgba(234, 234, 234, 1);
        border-radius: 5px;
        background-color: $white;
        cursor: pointer;
        @media screen and(max-width: 768px){
            row-gap: 0;
        }

    }
    &__link{
        padding-right: 16px;
        padding-left: 16px;
        padding-top: 16px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__read-more, &__hide-more{
        display: block;
        margin-top: 24px;
        font-family: "TTLakes-Regular";
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;
        @media screen and(max-width: 768px){
            display: none;
        }

    }
    &__hide-more{
        display: none;
    }
    .details__arrows{
        margin-right: 16px;
    }
    .hidden{
        display: none;
    }
    
}
.secondary-title{
    border-top: 1px solid rgba(217, 255, 245, 1);
    background: linear-gradient(90deg, #F8F8F8 0%, #FFFFFF 98.61%);
    font-family: "TTLakes-Regular";
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: rgba(126, 126, 130, 1);
    text-transform: uppercase;
    height: 56px;
    padding-top: 15px;

    @media screen and (max-width: 1120px) {
           
    }
}
